.home-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  padding: 80px 5%;
  background-color: #1a1a1a;
  color: white;
}

.content-section {
  flex: 1;
  padding-right: 5%;
  margin-top: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.image-section {
  flex: 1;
  position: relative;
  padding: 60px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  max-height: 80vh;
}

.image-section img {
  max-height: 70vh;
  width: auto;
  object-fit: contain;
}

.role-tags {
  position: fixed;
  top: 120px;
  left: 5%;
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 2rem;
  z-index: 100;
}

.role-tag {
  background: rgba(255, 255, 255, 0.1);
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: all 0.2s ease;
}

.role-tag:hover {
  background: rgba(255, 255, 255, 0.2);
}

.role-tag.active {
  background: #4A90E2;
}

.role-tag.active:hover {
  background: #357ABD;
}

.title {
  font-size: 1.8rem;
  font-weight: bold;
  margin-top: -40px;
  margin-bottom: 1.5rem;
  line-height: 1.4;
  font-family: 'Courier New', monospace;
  color: #4A90E2;
  white-space: pre-wrap;
}

.subtitle {
  font-size: 0.9rem;
  color: #a0a0a0;
  margin-bottom: 2rem;
  max-width: 600px;
}

.button-group {
  display: flex;
  gap: 1rem;
}

.button {
  padding: 1rem 2rem;
  font-size: 1.1rem;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.primary-button {
  background-color: #6c5ce7;
  color: white;
}

.primary-button:hover {
  background-color: #5b4bc4;
  transform: translateY(-2px);
}

.secondary-button {
  background-color: transparent;
  color: white;
  border: 2px solid #6c5ce7;
}

.secondary-button:hover {
  background-color: rgba(108, 92, 231, 0.1);
  transform: translateY(-2px);
}

/* Mobile Styles */
@media screen and (max-width: 768px) {
  .home-container {
    flex-direction: column;
    padding: 0;
    height: 100vh;
    position: relative;
    justify-content: flex-end;
    overflow: hidden;
  }

  .image-section {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 85vh;
    padding: 0;
    z-index: 1;
  }

  .image-section img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center 20%;
  }

  .content-section {
    position: absolute;
    bottom: 50px;
    left: 0;
    right: 0;
    z-index: 2;
    padding: 24px;
    background: linear-gradient(
      to bottom,
      rgba(26, 26, 26, 0) 0%,
      rgba(26, 26, 26, 0.95) 15%,
      #1a1a1a 30%
    );
    min-height: 280px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    margin-top: 0;
  }

  .role-tags {
    position: fixed;
    top: 80px;
    left: 24px;
    width: auto;
    padding: 0;
    z-index: 90;
    display: flex;
    justify-content: flex-start;
    background: none;
  }

  .role-tag {
    padding: 8px 16px;
    font-size: 0.85rem;
    white-space: nowrap;
    background: rgba(255, 255, 255, 0.12);
    backdrop-filter: blur(8px);
    -webkit-backdrop-filter: blur(8px);
    border-radius: 20px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  }

  .role-tag.active {
    background: rgba(74, 144, 226, 0.8);
    box-shadow: 0 2px 12px rgba(74, 144, 226, 0.3);
  }

  .title {
    font-size: 1.4rem;
    margin: 0 0 12px 0;
    line-height: 1.3;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
  }

  .subtitle {
    font-size: 0.95rem;
    margin: 0 0 20px 0;
    line-height: 1.4;
    opacity: 0.9;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  }

  .button-group {
    gap: 10px;
    margin-bottom: 8px;
  }

  .button {
    flex: 1;
    padding: 12px 16px;
    font-size: 0.9rem;
    border-radius: 12px;
    font-weight: 500;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
  }

  .primary-button {
    background: rgba(108, 92, 231, 0.95);
    backdrop-filter: blur(8px);
    -webkit-backdrop-filter: blur(8px);
  }

  .secondary-button {
    background: rgba(255, 255, 255, 0.1);
    border: 1.5px solid rgba(108, 92, 231, 0.5);
    backdrop-filter: blur(8px);
    -webkit-backdrop-filter: blur(8px);
  }

  .mobile-role-button {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: auto;
    padding: 10px 16px;
    background: rgba(108, 92, 231, 0.9);
    color: white;
    border: none;
    border-radius: 20px;
    font-size: 0.9rem;
    font-weight: 500;
    backdrop-filter: blur(8px);
    -webkit-backdrop-filter: blur(8px);
    box-shadow: 0 2px 12px rgba(108, 92, 231, 0.3);
    transition: all 0.3s ease;
    cursor: pointer;
  }

  .role-count {
    margin-left: 10px;
    font-size: 0.75rem;
    opacity: 0.9;
    background: rgba(255, 255, 255, 0.15);
    padding: 3px 8px;
    border-radius: 12px;
  }
}

.desktop-role-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.mobile-role-button {
  display: none;
}

@media screen and (max-width: 768px) {
  .desktop-role-tags {
    display: none;
  }

  .role-tags {
    position: fixed;
    top: 90px;
    left: 20px;
    width: auto;
    padding: 0;
    z-index: 90;
    display: flex;
    justify-content: flex-start;
    background: none;
  }

  .mobile-role-button {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: auto;
    padding: 10px 16px;
    background: rgba(108, 92, 231, 0.95);
    color: white;
    border: none;
    border-radius: 20px;
    font-size: 0.9rem;
    font-weight: 500;
    backdrop-filter: blur(8px);
    -webkit-backdrop-filter: blur(8px);
    box-shadow: 0 2px 12px rgba(108, 92, 231, 0.3);
    transition: all 0.3s ease;
    cursor: pointer;
  }

  .mobile-role-button:active {
    transform: scale(0.98);
  }

  .role-count {
    margin-left: 10px;
    font-size: 0.75rem;
    opacity: 0.9;
    background: rgba(255, 255, 255, 0.15);
    padding: 3px 8px;
    border-radius: 12px;
  }
}


