.header-container {
  background-color: #fff;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.04);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  font-family: 'Pretendard', sans-serif;
}

.header-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
}

.logo {
  font-size: 1.5rem;
  font-weight: 700;
  color: #1C3D5A;
  text-decoration: none;
  transition: color 0.2s ease;
  flex-shrink: 0;
}

.logo:hover {
  color: #4A90E2;
}

.search-container {
  position: relative;
  flex: 1;
  max-width: 500px;
}

.search-input {
  width: 100%;
  padding: 0.5rem 1rem;
  padding-right: 2.5rem;
  border: 1px solid #ddd;
  border-radius: 20px;
  font-size: 0.9rem;
  outline: none;
  transition: all 0.2s ease;
}

.search-input:focus {
  border-color: #4A90E2;
  box-shadow: 0 0 0 2px rgba(74, 144, 226, 0.1);
}

.search-input::placeholder {
  color: #999;
}

.search-button {
  position: absolute;
  right: 8px;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  color: #666;
  cursor: pointer;
  padding: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.search-button:hover {
  color: #4A90E2;
}

.nav {
  display: flex;
  gap: 2rem;
  align-items: center;
  flex-shrink: 0;
}

.nav-link {
  color: #1C3D5A;
  text-decoration: none;
  font-weight: 500;
  font-size: 1rem;
  padding: 0.5rem;
  transition: all 0.2s ease;
  position: relative;
}

.nav-link:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #4A90E2;
  transition: width 0.2s ease;
}

.nav-link:hover {
  color: #4A90E2;
}

.nav-link:hover:after {
  width: 100%;
}

.logout-button {
  background: none;
  border: none;
  color: #1C3D5A;
  font-weight: 500;
  font-size: 1rem;
  padding: 0.5rem;
  cursor: pointer;
  transition: all 0.2s ease;
  font-family: inherit;
}

.logout-button:hover {
  color: #4A90E2;
}

.mobile-menu-button {
  display: none;
  background: none;
  border: none;
  padding: 0.5rem;
  cursor: pointer;
  color: #1C3D5A;
}

.social-icons {
  display: flex;
  gap: 1rem;
  align-items: center;
  margin-right: 1rem;
}

.social-link {
  color: #1C3D5A;
  font-size: 1.2rem;
  transition: color 0.2s ease;
  display: flex;
  align-items: center;
}

.social-link:hover {
  color: #4A90E2;
}

/* 모바일 스타일 */
@media (max-width: 768px) {
  .header-content {
    padding: 1rem;
    position: relative;
  }

  .search-container {
    display: none;
  }

  .nav {
    display: none;
  }

  .nav.active {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.98);
    padding: 4rem 2rem;
    z-index: 1001;
    align-items: center;
    justify-content: flex-start;
    padding-top: 6rem;
  }

  .nav.active .nav-link {
    color: white;
    font-size: 2rem;
    margin: 0.8rem 0;
    text-align: center;
    letter-spacing: 1px;
  }

  .nav.active .nav-link:after {
    background-color: white;
    height: 1px;
  }

  .nav.active .logout-button {
    color: white;
    font-size: 1.5rem;
    margin-top: 2rem;
    opacity: 0.8;
  }

  .mobile-menu-button {
    display: block;
    z-index: 1002;
    position: relative;
    padding: 0.8rem;
    background: rgba(0, 0, 0, 0.2);
    border-radius: 50%;
    width: 45px;
    height: 45px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background 0.3s ease;
    margin-left: auto;
  }

  .mobile-menu-button:hover {
    background: rgba(0, 0, 0, 0.3);
  }

  .nav.active ~ .mobile-menu-button {
    position: fixed;
    top: 1.5rem;
    right: 1.5rem;
    color: white;
    background: rgba(255, 255, 255, 0.1);
  }

  .nav.active ~ .mobile-menu-button:hover {
    background: rgba(255, 255, 255, 0.2);
  }

  .social-icons {
    display: none;
  }

  .nav.active .social-icons {
    display: flex;
    justify-content: center;
    margin-top: auto;
    margin-bottom: 3rem;
    gap: 2.5rem;
    width: 100%;
    padding: 0 2rem;
  }

  .nav.active .social-link {
    color: white;
    font-size: 2rem;
    opacity: 0.8;
    transition: opacity 0.3s ease, transform 0.3s ease;
  }

  .nav.active .social-link:hover {
    opacity: 1;
    transform: translateY(-2px);
  }
} 